import { Component, OnInit , ViewChild, ElementRef} from '@angular/core';
import { CourseService } from '../service/firebase-service.service';
import { AuthServiceService } from '../service/auth-service.service';
import { AngularFireAuth } from 'angularfire2/auth';
import 'rxjs/add/operator/take';
import {ActivatedRoute, Router} from '@angular/router';
import {AngularFireDatabase} from 'angularfire2/database';
import { FirebaseListObservable, FirebaseObjectObservable } from 'angularfire2/database-deprecated';
import * as jsPDF from 'jspdf';


@Component({
  selector: 'app-mem-history',
  templateUrl: './mem-history.component.html',
  styleUrls: ['./mem-history.component.css']
})
export class MemHistoryComponent implements OnInit {

  @ViewChild('content') content: ElementRef;
  
  reserve : string = "/reservation"
  course : string = "/course"
  userPath: string = "/users"
  
  reserveList: any = {};
  courseList : any;

  reserveTemp: any;
  id;
  temp : any={};

  c1 : any={};

  status:any=[];
  tmp: any=[];
  ctmp: any=[];
  course_: any;
  userID: any={};
  reserveCourse: any = {};

  date:any=[];
  userData = {
    name:'',
    lastName:'',
    address:'',
    tel:'',
    email:''
  }
  currentUser:any;
  courseK:any;
  index:any;
  constructor(private CourseService: CourseService,private angularFireAuth:AngularFireAuth,
    private auth:AuthServiceService,private route: ActivatedRoute,private db: AngularFireDatabase,
  private router: Router) { }

   ngOnInit() {
    this.id =  this.route.snapshot.paramMap.get("id");

    this.temp = this.CourseService.getCourseList('/course')
    // console.log(this.temp)
    this.getUserByKey();
    this.reserveList =  this.CourseService.getCourseList(this.reserve).subscribe(data=>{
      let c: any=[];
      let s: any=[];
      // let u = this.auth.currentUserId;
      let k = 0;
      let d: any=[];
      Object.keys(data).map(function(key,index) {
        c[key] = data[key]['c_id']
        d[key] = data[key]['c_date']
        s[key] = data[key]['status']
        if(data[key]['status'] == 1){
          s[key] = 'ยืนยันการจองแล้ว'
        }else{
          s[key] = 'รอการยืนยัน'
        }
        data[key] = data[key]['u_id'];
      });
      this.status = s;
      this.date = d;
      console.log(this.status);
      this.course_ = c;
    // console.log("this C: "+ this.course_)
    this.reserveCourse = data;   
    let i = 0;
    let j = 0;
      for(let res of this.reserveCourse) {
        if(res == this.id) {
          this.ctmp[j] = this.course_[i];
          j++;
        }
        i++
      }

      this.courseList = this.ctmp

        this.getCoursesByKey();

    });
    
    
  

    
  }
  

  editProfile(){
    this.router.navigate([`/profile-detail/${this.auth.currentUserId}`])
  }
  myHistory(){
    this.router.navigate([`/mem-history/${this.auth.currentUserId}`])
  }
  myFavoriteCourse(){
    this.router.navigate([`/mem-fav-course/${this.auth.currentUserId}`])
  }

  getCoursesByKey() {
    let i = 0;
    for(let c of this.courseList) {
      this.db.object(this.course+'/'+c).valueChanges().subscribe(data =>{
        console.log(data);
        this.c1[c] = data;
        i++;
      });
      
    }
  }

  public pdfDownload(x,i){
    this.getUserByKey();
    this.getCoursesByKey();
    this.courseK = x;
    this.index = i;
    console.log(this.date[this.index]);
    // console.log(this.c1[this.courseK].C_Name);
    let img = "data:image/png;base64,"
    
    let doc = new jsPDF();
    doc.addImage(img, 'PNG',
  85,0,50,50);
    doc.addFont('ComicSansMS', 'Tahoma', 'normal');
        doc.setFont('Tahoma');
        // doc.text(50,50,'Hello World');
    let specialElement = {
      '#editor' : function(element,renderer){
        return true;
      }
    };

    let content = this.content.nativeElement;

    doc.fromHTML(content.innerHTML,15,15,{
      'width' : 190,
      'elementHandler' : specialElement
    });

    doc.save('test.pdf')

  }

  getUserByKey() {
    this.currentUser = this.auth.currentUserId;
      this.db.object(this.userPath+'/'+this.auth.currentUserId).valueChanges().subscribe(data =>{
        this.userData = data[this.auth.currentUserId];
        // console.log(this.userData[this.currentUser]);
      });
  }




}



